.title-header-copy {
  position: absolute;
  top: 20%;
  transform: translate(0, -20%);
  text-align: center;
  width: 100%;
}

.title-header-copy h3 {
  font-family: sans-serif;
  font-size: 320%;
  text-transform: uppercase;
}

.title-header-copy ul {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 3rem;
}

.title-header-copy ul li button {
  background: none;
  border: 3px solid;
  color: inherit;
  border-radius: 5rem;
  font-size: 90%;
  text-transform: uppercase;
  padding: 0.25rem 2rem;
}

.title-header-copy ul li button.active {
  background: inherit;
  color: inherit;
}


@media screen and (max-width: 1200px) {
  .title-header-copy h3 {
    font-size: 190%;
  }
}

@media screen and (max-width: 768px) {
  .title-header-copy h3 {
    font-size: 160%;
  }
}
